<template>
  <div id="video_detail">
    <new-layout>
      <template slot="top">
        <video-header>{{ intro.name }}</video-header>

        <!--视频插件-->
        <new-video-play></new-video-play>
      </template>
      <template slot="center">
        <div class="video_desc">
          <div class="tit-date">
            <span>简介：</span>
            <span>发布时间：{{ timeStamp(intro.publishTime) }}</span>
          </div>
          <div class="con">
            <p>{{ intro.summary ? intro.summary : '当前视频无相关简介！' }}</p>
          </div>
        </div>

        <!--猜你喜欢-->
        <new-favourite favor="intro"></new-favourite>
      </template>
    </new-layout>
  </div>
</template>

<script>
    export default {
      name:"detail",
      data(){
        return{
          intro:''
        }
      },

      watch:{
        async $route(to, from){
          if(to.query.id !== from.query.id){
            let res = await this.$http.get(this.$HOST + '/open/health/message/center/app/play?id=' + to.query.id);
            this.intro = res.result.video;
          }
        }
      },

      async created(){
        let that = this;
        let id = this.$route.query.id;
        let res = await this.$http.get(this.$HOST + '/open/health/message/center/app/play?id=' + id);
        that.intro = res.result.video;
      }
    }
</script>

<style scoped lang="scss">
  #video_detail{
    .video_desc{
      background: #FFF;
      margin: 30px 0;
      padding: 30px;

      .tit-date{
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding-bottom: 10px;

        span{
          color: #6EAFFF;
          &:first-child{
            font-size: 32px;
          }
          &:last-child{
            font-size: 27px;
          }
        }
      }

      .con{
        p{
          color: #8D8D8D;
          font-size: 28px;
        }
      }
    }
  }
</style>



